import ECharts from 'vue-echarts'
import 'echarts/lib/chart/pie'
import 'echarts/lib/component/tooltip'
import { fmtMoney } from '@/filters'

export default {
  components: {
    'v-chart': ECharts
  },
  props: {
    datas: {
      type: Array,
      default: () => []
    }
  },
  data () {
    return {
      options: { // 饼图通用配置
        tooltip: {
          trigger: 'item',
          formatter: '{b}<br/>积分：{c}<br/>占比：{d}%',
          backgroundColor: '#fff',
          borderColor: '#909399',
          borderWidth: 1,
          textStyle: {
            color: '#303133',
            fontSize: 13
          }
        },
        series: []
      },
      totalProperty: 'integral', // 中间总计的列属性值
      chartValue: 'integral' // 饼图展示的依据属性值
    }
  },
  computed: {
    total () { // 饼图中间总计
      return this.datas.reduce((sum, cur) => sum + cur[this.totalProperty], 0)
    }
  },
  watch: {
    datas: { // 数据的检测与图表更新
      handler: function (val) {
        this.options.series[0].data = val.map(d => {
          return {
            value: d[this.chartValue],
            name: d.name
          }
        })
      },
      immediate: true,
      deep: true
    }
  },
  methods: {
    getSummaries (param) { // 表格统计的自定义方法
      const { columns, data } = param
      const sums = []

      columns.forEach((column, index) => {
        if (index === 0) {
          sums[index] = '总计'
          return
        }
        const values = data.map(item => Number(item[column.property]))
        if (this.isMatchCondition(column) && !values.every(value => isNaN(value))) {
          sums[index] = fmtMoney(values.reduce((prev, curr) => {
            const value = Number(curr)
            if (!isNaN(value)) {
              return prev + curr
            } else {
              return prev
            }
          }, 0))
        } else {
          sums[index] = ''
        }
      })

      return sums
    },

    isMatchCondition (column) { // 判断是否需要总计的属性值，默认不判断
      return true
    }
  }
}
